/* Normal desktop :1200px. */
@media (min-width:1200px) {
    .container {
        max-width: 1170px;
    }
}

/* Normal desktop :992px. */
@media (min-width:992px) {
    /* .widget-area {
        padding-left: 30px;
    } */

    .col-1-5 {
        -ms-flex: 0 0 20%;
        flex: 0 0 20%;
        max-width: 20%;
    }

    .col-2-5 {
        -ms-flex: 0 0 40%;
        flex: 0 0 40%;
        max-width: 40%;
    }

    .col-3-5 {
        -ms-flex: 0 0 60%;
        flex: 0 0 60%;
        max-width: 60%;
    }

    .col-4-5 {
        -ms-flex: 0 0 80%;
        flex: 0 0 80%;
        max-width: 80%;
    }

    .slicknav_nav {
        border-bottom: 1px solid #f4f5f9;
        border-top: 1px solid #f4f5f9;
        box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.05);
    }

    .entry-wraper,
    .entry-header.entry-header-style-1,
    .single-comment {
        max-width: 800px;
        margin: 0 auto;
    }
}

/* Tablet desktop :768px. */
@media (min-width:768px) {
    .slicknav_nav li {
        display: block;
        max-width: 720px;
        margin: 0 auto;
        position: relative;
    }
}

/* small mobile :992px. */
@media (max-width:992px) {
    .comment-form .email {
        padding-left: 0px;
    }

    .logo,
    .header-tools {
        position: relative;
        z-index: 999
    }

    .preloader::before,
    .counter {
        font-size: 8em
    }
}

@media (max-width:991px) {
    button.search-icon {
        display: block !important;
    }

    form.search-style-2 {
        display: none;
    }

    .form-search-wrapper {
        display: none;
    }

    .header-top .header-top-right .header-tools {
        display: none;
    }

    .header-sticky .header-tools button.search-icon {
        display: none !important;
    }

    .header-sticky .header-tools .header-social-network {
        display: block !important;
    }

    .header-sticky .header-tools .off-canvas-toggle-cover {
        display: block !important;
    }

    .slicknav_nav {
        box-shadow: 0px 15px 20px -15px #000;
    }
}

/* small mobile :768px. */
@media (max-width:767px) {
    .comment-form .name {
        padding-right: 0px;
        margin-bottom: 1rem;
    }
}

/* Large Mobile :480px. */
@media (max-width:480px) {
    .comments-area {
        padding: 50px 8px;
    }

    .vertical-divider>[class*="col-"]:nth-child(n+2)::after {
        content: none;
    }

    .mb-sm-4 {
        margin-bottom: 1.5rem !important;
    }

    .mb-sm-3 {
        margin-bottom: 1rem !important;
    }

    .mt-sm-4 {
        margin-top: 1.5rem !important;
    }

    .mt-sm-3 {
        margin-top: 1rem !important;
    }

    .pl-0 {
        padding-left: 15px !important;
    }

    .col-xs-6 {
        -ms-flex: 0 0 50%;
        flex: 0 0 50%;
        max-width: 50%;
    }

    .home-featured-1 .img-hover-slide {
        min-height: 450px;
    }

    .preloader::before {
        font-size: 3em
    }

    .loop-list-style-1 .post-thumb {
        margin-bottom: 15px;
    }

    .featured-1 h2 {
        font-size: 30px;
        font-weight: 900;
    }

    .entry-header .entry-title {
        font-size: 2.2em;
    }

    .featured-slider-2-items .post-thumb .thumb-overlay {
        min-height: 450px;
    }

    .featured-slider-2 .post-content-overlay .post-title {
        max-width: 100%;
    }

    .featured-slider-2-nav {
        display: none
    }

    .logo__custom img {
        max-width: 38px;
    }

    .logo__custom .logo__custom-text {
        margin-bottom: 0;
    }

    .logo__custom .logo__custom-text span {
        font-size: 28px;
    }

    .logo__custom .logo__custom-text small {
        bottom: -8px;
    }

    .font-x-small {
        font-size: 14px;
    }

    .font-small {
        font-size: 16px;
    }

    .font-medium {
        font-size: 18px;
    }

    .post-card-1 .post-content {
        padding: 20px!important;
        padding-top: 5px!important;
    }

    .post-card-1 .post-content .entry-meta {
        margin-bottom: 5px!important;
    }

    .post-card-1 .post-content .entry-meta a {
        display: inline-block;
        padding: 16px 0;
    }

    .text-limit-2-row {
        -webkit-line-clamp: 3!important;
    }

    .carausel-3-columns-wrapper .carausel-3-columns h6 {
        font-size: 18px;
    }

    .widget-header-1 h5, .widget-header-2 h5 {
        font-size: 16px;
    }

    .comments-title, .be-comment-block .star-title {
        font-size: 18px;
    }

    .help-block {
        font-size: 16px;
    }

    .image-upload__uploader, .image-viewer__item {
        width: 90px;
        height: 90px;
    }

    .image-upload__text {
        font-size: 16px;
        line-height: 18px;
    }

    .image-upload__icon {
        font-size: 22px!important;
    }

    .form-subcriber input.form-control, .form-subcriber .btn {
        font-size: 16px;
    }

    .slicknav_nav {
        font-size: 16px;
    }
}

/*Masonry layout*/
@media (max-width:575px) {

    .grid-sizer,
    .grid-item {
        width: 100%;
    }

    .grid-line {
        display: none
    }
}

@media (min-width:991px) and (max-width:1200px) {
    .font-menu {
        font-size: 14px;
    }
    .main-menu>li {
        padding: 0 5px;
    }
}

@media (min-width:576px) and (max-width:767px) {

    .grid-sizer,
    .grid-item {
        width: 50%;
    }
}

@media (min-width:768px) {

    .grid-sizer,
    .grid-item {
        width: 33.333%;
    }
}
