/*** Styling for widgets ***/

/*Widget header*/
.widget-header-1 h5,
.widget-header-2 h5 {
    font-size: 15px;
    text-transform: uppercase;
    letter-spacing: 1px;
    border-bottom: 1px solid var(--mutted-border-color);
    padding-bottom: 10px;
    position: relative;
    font-weight: bold;
    color: var(--color-primary);
}

.widget-header-2 h5::after {
    content: "";
    background: var(--color-dark);
    height: 3px;
    width: 50px;
    position: absolute;
    bottom: 0;
    left: 0;
}

.widget-header-3 h5 {
    text-transform: uppercase;
    font-size: 15px;
    margin-bottom: 0;
    font-weight: bold;
    color: var(--color-primary);
}

/*Widget sidebar menu*/
.sidebar-widget ul.menu li {
    padding: 5px 0;
    position: relative;
    font-size: 14px;
}

#sidebar-wrapper ul.sub-menu {
    padding: 0 !important;
    background: none;
}

.sidebar-widget ul.menu li:last-child {
    border: 0;
}

/*Tagcloud*/
.widget_tagcloud .tag-cloud-link {
    -moz-user-select: none;
    text-transform: capitalize;
    display: inline-block;
    font-size: 15px;
    font-weight: 400;
    line-height: 0;
    padding: 15px 20px;
    border-radius: 30px;
    margin: 0 5px 10px 0;
    border: 1px solid #ebebeb;
    background: #f2f3f5;
}

/*Widget about*/
.about-author-img {
    border-radius: 50%;
    width: 64px;
    height: 64px;
    box-shadow: 0 2px 4px var(--button-shadow-color-normal), 0 0 0 transparent
}

/*Login form*/
.toggle_info {
    padding: 20px;
    background-color: #F7F7F7;
}

.login_form .panel-body,
.coupon_form .panel-body {
    border: 1px solid #e6e6e6;
    padding: 30px;
    margin-top: 30px;
}

.custome-radio .form-check-label,
.custome-checkbox .form-check-label {
    position: relative;
    cursor: pointer;
}

.custome-checkbox .form-check-label {
    position: relative;
    cursor: pointer;
    color: #687188;
    padding: 0;
    vertical-align: middle;
}

.custome-radio .form-check-input,
.custome-checkbox .form-check-input {
    display: none;
}

.custome-checkbox .form-check-label::before {
    content: "";
    border: 2px solid #ced4da;
    height: 17px;
    width: 17px;
    margin: 0px 8px 0 0;
    display: inline-block;
    vertical-align: middle;
    border-radius: 2px;
}

.custome-checkbox .form-check-label span {
    vertical-align: middle;
}

.custome-checkbox input[type="checkbox"]:checked+.form-check-label::after {
    opacity: 1;
}

.custome-checkbox input[type="checkbox"]+.form-check-label::after {
    content: "";
    width: 11px;
    position: absolute;
    top: 50%;
    left: 3px;
    opacity: 0;
    height: 6px;
    border-left: 2px solid #fff;
    border-bottom: 2px solid #fff;
    -moz-transform: translateY(-65%) rotate(-45deg);
    -webkit-transform: translateY(-65%) rotate(-45deg);
    transform: translateY(-65%) rotate(-45deg);
}

.login_footer {
    margin-bottom: 20px;
    margin-top: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: justify;
    justify-content: space-between;
    width: 100%;
}

.divider-text-center {
    text-align: center;
    position: relative;
}

.divider-text-center::before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    right: 0;
    border-top: 1px solid #ddd;
    -moz-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
}

.divider-text-center span {
    background-color: #fff;
    padding: 0 15px;
    position: relative;
    text-transform: uppercase;
}

.btn-login li {
    margin: 0px 5px 0;
    display: inline-block;
}

.btn-login li a {
    border-radius: 5px;
    padding: 15px 25px;
    color: #fff;
    display: block;
    line-height: 1;
    text-transform: none;
    letter-spacing: 0;
    font-size: 1rem;
}

.btn-facebook {
    background: #3b5998;
    border-color: #3b5998;
}

.btn-google {
    background: #d85040;
    border-color: #d85040;
}

.custome-checkbox input[type="checkbox"]:checked+.form-check-label::before {
    background-color: var(--color-primary);
    border-color: var(--color-primary);
}

/*Newsletter*/
.form-subcriber input.form-control {
    padding: 16px 25px;
    line-height: 24px;
    border-radius: 30px 0 0 30px;
    max-width: 300px;
    font-size: 0.875rem;
}

.form-subcriber .btn {
    font-size: 14px;
    border-radius: 0 30px 30px 0;
    padding: 0 25px;
}

input.form-control::placeholder {
    color: #9b9b9b
}

.form-newsletter-cover {
    display: table;
    width: 100%;
}

.form-newsletter {
    display: flex;
    align-content: center;
    align-items: center;
    border: 1px solid rgb(225, 225, 225);
    padding: 0 14px;
    width: 100%;
    border-radius: 2px;
}

.form-newsletter input[type="email"] {
    border: 0;
    background: none;
    width: calc(100% - 50px);
    padding-left: 0;
    padding-right: 0;
    font-size: 14px;
}

.form-newsletter button,
.exit-reading-mode {
    background: none;
    padding: 0;
    height: 38px;
    text-decoration: none;
    border: 0;
}

.form-newsletter button .long-arrow,
button.exit-reading-mode .long-arrow {
    width: 50px;
}

button.box-shadow {
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
}

/*instagram*/
.widget_instagram .insta-feed {
    padding-top: 5px;
    margin: -2px;
    display: flex;
    flex-wrap: wrap;
}

.widget_instagram .insta-feed li {
    width: 29.33%;
    margin: 5px;
}

.widget_instagram .insta-feed li img {
    width: 100%;
}

/*Entry meta*/
.entry-meta {
    line-height: 1;
    color: #666;
}

.entry-meta.meta-2 .author-img img {
    width: 22px;
    height: 22px;
    border-radius: 50%;
}

.entry-meta.meta-2 .author-name {
    font-weight: 600;
    display: inline-block;
    margin-top: 7px;
}

.entry-meta.meta-2 .create-date {
    position: relative;
}

.entry-meta.meta-2 .create-date::after {
    content: "";
    width: 1px;
    background: #999;
    height: 100%;
    position: absolute;
    left: -15px;
    top: 0;
    transform: rotate(21deg);
}

figure .post-format {
    background: #fff;
    display: block;
    width: 25px;
    height: 25px;
    text-align: center;
    line-height: 28px;
    border-radius: 2px;
}

.post-thumb-80 img {
    width: 90px;
    height: 80px;
    object-fit: cover;
}

.post-thumb-64 img {
    width: 64px;
    height: 64px;
    border-radius: 50%;
}

.position-left-bottom {
    position: absolute;
    bottom: 15px;
    left: 15px;
    z-index: 1
}

.position-top-right {
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 1
}

.entry-meta.meta-0 span {
    position: relative;
}

.entry-meta.meta-1 span {
    margin-right: 10px;
}

span.has-dot {
    position: relative;
    padding-left: 10px;
}

span.has-dot::before {
    content: "";
    width: 3px;
    height: 3px;
    background: #999;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    margin-top: -1px;
    display: block;
    left: -3px;
    border-radius: 50%
}

/*FEATURED*/

/*featured-slider-1*/
.featured-1 {
    padding: 80px 0;
    background: #f1f2f3;
}

.featured-1 h3 {
    font-size: 42px;
    font-weight: 900;
}

.featured-1 h2 {
    font-size: 50px;
    font-weight: 900;
}

.featured-1 h2 span {
    color: var(--color-primary);
}

.tags {
    letter-spacing: 0.5px
}

/*featured-slider-2*/
.featured-slider-2-items .post-thumb .thumb-overlay,
.featured-slider-3-items .post-thumb .thumb-overlay {
    min-height: 600px;
    background-position: center;
}

.featured-slider-2 .post-content-overlay {
    position: absolute;
    transition-duration: 0.2s;
    width: 100%;
    bottom: 80px;
    z-index: 500;
}

.featured-slider-2 .post-content-overlay .post-title {
    max-width: 50%;
}

.featured-slider-2-nav-cover {
    position: absolute;
    bottom: 80px;
    right: 0;
    width: 33%;
}

.featured-slider-2-nav .slick-current img {
    border: 3px solid rgba(255, 255, 255, 0.7);
}

.featured-slider-2 .arrow-cover {
    position: absolute;
    left: 0;
    z-index: 300;
}

.featured-slider-2 .arrow-cover i {
    color: #fff;
}

/*featured-slider-3*/
.featured-slider-3 .post-content-overlay {
    position: absolute;
    transition-duration: 0.2s;
    width: 70%;
    z-index: 500;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    text-align: center;
}

.slider-3-arrow-cover {
    position: absolute;
    z-index: 9;
    bottom: 20px;
    text-align: center;
    width: 100%;
}

/*SLICK SLIDER*/
.transition-04s {
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -ms-transition: all 0.4s ease;
}

.img-hover-slide {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 280px;
    overflow: hidden;
}

.post-thumb:hover .img-hover-slide {
    background-position: 30%;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -ms-transition: all 0.4s ease;
}

.post-module-1 .post-content-overlay {
    position: absolute;
    bottom: 0;
    transition-duration: 0.2s;
}

.post-module-1 .post-thumb {
    -webkit-box-shadow: 4px 4px 0 var(--bg-gray);
    box-shadow: 4px 4px 0 var(--color-grey);
}

.post-module-1 .post-thumb:hover .post-content-overlay {
    bottom: 5px;
}

.post-module-1 .post-content-overlay .entry-meta {
    line-height: 1;
}

.post-module-1 .post-content-overlay .entry-meta span.post-cat,
.entry-meta.meta-0 span.post-cat {
    padding-right: 7px;
    margin-right: 10px;
    font-weight: bold;
    color: #666;
}

.post-module-1 .post-content-overlay .entry-meta span.post-cat::after,
.entry-meta.meta-0 span.post-cat::after {
    content: "";
    width: 3px;
    height: 3px;
    background: #999;
    position: absolute;
    right: 0;
    bottom: 3px;
    border-radius: 50%
}

.post-module-1 .post-content-overlay .entry-meta.text-white span.post-cat::after {
    background: #fff;
}

.slick-dots {
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.slick-dots li {
    margin: 0 0.15rem;
}

.slick-dots button {
    display: block;
    width: 6px;
    height: 6px;
    padding: 0;
    border: none;
    background-color: #999;
    text-indent: -9999px;
}

.slick-dots li.slick-active button {
    background-color: #111;
}

.top-left-icon,
.top-right-icon {
    position: absolute;
    top: 30px;
    left: 30px;
    border-radius: 50px;
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    font-size: 12px;
    z-index: 3;
    color: #fff;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    transition-duration: 0.4s;
    -ms-transition: all 0.4s ease;
}

.top-right-icon {
    left: unset;
    right: 30px;
}

.img-hover-slide:hover .top-left-icon,
.img-hover-slide:hover .top-right-icon {
    margin-top: -3px;
    -webkit-box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
    -moz-box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
    box-shadow: 0px 5px 11px 0px rgba(0, 0, 0, 0.18), 0px 4px 15px 0px rgba(0, 0, 0, 0.15);
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    transition-duration: 0.4s;
    -ms-transition: all 0.4s ease;
}

.hover-up:hover {
    transform: translateY(-5px);
    /* box-shadow: var(--box-shadow-hover), 0 0 0 transparent; */
}

.hover-up-2:hover {
    transform: translateY(-5px);
}

.transition-normal {
    transition: all .25s cubic-bezier(.02, .01, .47, 1);
}

/*post-card-1*/
.post-card-1 {
    border: 1px solid var(--secondary-border-color);
    z-index: 12;
    transition: all .25s cubic-bezier(.02, .01, .47, 1);
    position: relative;
    background: #fff
}

.post-card-1::before {
    content: "";
    position: absolute;
    top: 10px;
    right: 10px;
    bottom: 0;
    left: 10px;
    border-radius: 10px;
    box-shadow: var(--box-shadow-normal), 0 0 0 transparent;
    transition: all .25s cubic-bezier(.02, .01, .47, 1);
    z-index: -1;
}

.post-card-1 .thumb-overlay {
    min-height: 220px;
    border-radius: 10px 10px 0 0;
}

.post-card-1 .post-content {
    background: #fff;
    border-radius: 0 0 10px 10px;
}

.post-card-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    min-height: 115px;
}

.post-card-1 .top-right-icon {
    top: 20px;
    right: 20px;
}

.post-card-1 .top-left-icon {
    top: 20px;
    left: 20px;
}

/*Carausel-1*/
.carausel-post-1 .img-hover-slide {
    min-height: 420px;
}

.thumb-overlay::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, transparent), to(rgba(0, 0, 0, .8)));
    background: -webkit-linear-gradient(top, transparent 50%, rgba(0, 0, 0, .8) 100%);
    background: -o-linear-gradient(top, transparent 50%, rgba(0, 0, 0, .8) 100%);
    background: linear-gradient(to bottom, transparent 50%, rgba(0, 0, 0, .8) 100%);
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -ms-transition: all 0.4s ease;
}

.carausel-post-1 .arrow-cover {
    position: absolute;
    bottom: 30px;
    right: 15px;
    z-index: 99
}

.arrow-cover button,
.slider-3-arrow-cover button {
    background: none;
    border: 0;
}

.arrow-cover button i,
.slider-3-arrow-cover button i {
    color: white;
    font-size: 22px
}

.carausel-post-1 .post-content-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    transition-duration: 0.2s;
}

.carausel-post-1:hover .post-content-overlay {
    bottom: 15px;
    transition-duration: 0.2s;
}

/*Carausel-3*/
.carausel-3-columns {
    margin: 0 -15px;
}

.carausel-3-columns-item {
    margin-right: 15px;
    margin-left: 15px;
}

/*POST MODULES*/

/*list style*/

.list-style-2 .post-thumb .img-hover-slide {
    min-height: 150px
}

/*post-share-social*/
figure .social-share,
.post-thumb .social-share {
    position: absolute;
    float: left;
    overflow: hidden;
    padding-left: 0;
    bottom: 10px;
    right: 0;
    margin: 0;
    z-index: 200;
}

.animate-conner .post-thumb .social-share {
    right: 35px;
}

figure .social-share li,
.post-thumb .social-share li {
    width: 100%;
    text-align: center;
    max-width: 0px;
    opacity: 0;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    float: left;
    margin-left: 5px;
}

figure .social-share li:first-child,
.post-thumb .social-share li:first-child {
    max-width: 24px;
    opacity: 1;
    pointer-events: none;
}

figure .social-share li:first-child,
.post-thumb .social-share li:first-child a,
.post-thumb .social-share li:first-child span {
    background: #007aff;
}

figure .social-share li:first-child,
.post-thumb .social-share li:first-child a i {
    line-height: unset;
}

figure .social-share li a,
.post-thumb .social-share li a,
.post-thumb .social-share li span {
    display: block;
    width: 20px;
    height: 20px;
    line-height: 24px;
    text-align: center;
    border-radius: 50%;
    font-size: 11px;
    color: #fff;
    cursor: pointer;
}

.post-thumb .social-share li:first-child a,
.post-thumb .social-share li:first-child span {
    width: 24px;
    height: 24px;
}

.post-thumb .social-share li:first-child span {
    display: flex;
    align-items: center;
    justify-content: center;
}

figure .social-share:hover li:nth-child(1),
.post-thumb .social-share:hover li:nth-child(1) {
    opacity: 0;
    max-width: 0;
}

figure .social-share:hover li,
.post-thumb .social-share:hover li {
    opacity: 1;
    max-width: 20px;
}

figure .social-share:hover li:last-child,
.post-thumb .social-share:hover li:last-child {
    margin-right: 15px;
}

.social-share {
    height: 0;
    transition-duration: 0.2s;
}

.bgcover:hover .social-share,
.post-thumb:hover .social-share {
    height: 30px;
    transition-duration: 0.2s;
}

