.header-social-network .list-inline-item a.fb {
    background: #3b5999;
} */

/* .post-thumb .social-share li a.tw,
.header-social-network .list-inline-item a.tw {
    background: #55acee;
} */

.post-thumb .social-share li a.pt,
.header-social-network .list-inline-item a.pt {
    background: #bd081c;
}

.thumb-overlay .post-thumb .social-share {
    right: 35px;
}

/*More articles*/
.single-more-articles {
    background: #fff none repeat scroll 0 0;
    border: 1px solid rgb(225, 225, 225);
    bottom: 84px;
    display: inline-block;
    max-width: 100%;
    opacity: 1;
    padding: 0;
    position: fixed;
    right: 20px;
    transform: translateY(200%);
    transition: all .2s cubic-bezier(.4, 0, .2, 1) 0s;
    width: 330px;
    z-index: 98;
    padding: 20px;
    -webkit-box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.05);
    box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.05);
    -webkit-box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.05);
}

.single-more-articles.single-more-articles--visible {
    transform: none;
    opacity: 1;
}

button.single-more-articles-close {
    position: absolute;
    top: -20px;
    right: -12px;
    border: 0;
    background: none;
    font-size: 22px;
}

/*news-flash*/
.news-flash-cover .flash-news-title {
    line-height: 1;
    margin-bottom: 0;
    float: left;
    margin-right: 15px;
    color: #fff;
    background: #000;
    padding: 2px 10px;
    border-radius: 2px;
}

.news-flash-cover .flash-news-title i {
    font-size: 12px;
}

.news-flash-cover ul {
    line-height: 1;
}

#date-time ul {
    right: 0;
}

.live-now {
    color: #df4a2c;
    padding-left: 10px;
    margin-right: 10px;
    position: relative;
    letter-spacing: 0.5px;
}

.live-now::before {
    content: "";
    background: #b50f0f;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -3px;
    animation: shadow-pulse 1s infinite;
}

/*Categories*/
.widget_categories li.cat-item,
.widget_archive li,
.widget_pages li,
.widget_recent_comments li,
.widget_nav_menu li {
    border-bottom: 1px dotted rgba(0, 0, 0, 0.15);
    display: table;
    width: 100%;
    padding: 8px 0;
}

.widget_categories li.cat-item:last-child {
    border: none;
}

.widget_categories li.cat-item a,
.widget_archive li a,
.widget_pages li a {
    text-align: left;
    padding: 0;
}

.menu li.menu-item-has-children {
    position: relative;
}

.menu li.menu-item-has-children::after {
    content: "\33";
    font-family: 'ElegantIcons';
    font-size: 14px;
    display: inline-block;
    padding-left: 5px;
    position: absolute;
    right: 0;
}

#sidebar-wrapper .menu li.menu-item-has-children ul.sub-menu {
    padding-left: 15px !important;
}

.widget_categories li.cat-item span.post-count {
    float: right;
    display: inline-block;
    width: 24px;
    height: 24px;
    background: var(--color-muted);
    border-radius: 50%;
    line-height: 24px;
    text-align: center;
    color: #fff;
    font-size: 12px;
}

.widget_categories li:hover span.post-count {
    background: var(--color-danger);
}

/*Tab*/
.nav-tabs .nav-link {
    color: #666;
    font-weight: 600;
}

.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
    color: #000;
}

.nav-tabs .nav-link.active,
.nav-tabs .nav-link,
.nav-tabs .nav-link:focus,
.nav-tabs .nav-link:hover {
    border: none;
    padding: 0;
    margin-right: 30px;
}

.nav-tabs .nav-link.active {
    border-width: 0 0 2px 0;
    border-color: #000;
    border-style: solid;
}

nav.tab-nav .nav-tabs {
    border: none;
}

.tab-content>.tab-pane {
    display: block;
    height: 0px;
    overflow: hidden;
}

.tab-content>.active {
    height: auto;
}

/*Social network widget*/

/*Taber*/
.widget-taber a.nav-link {
    font-size: 14px;
    padding: 4px 15px 0 0;
    float: left;
    position: relative;
}

.widget-taber a.nav-link.active {
    font-size: 18px;
    padding-top: 0;
    padding-left: 15px;
}

.widget-taber a.nav-link.active::before {
    content: "";
    width: 8px;
    height: 8px;
    display: block;
    position: absolute;
    border-radius: 50%;
    left: 0;
    top: 50%;
    margin-top: -4px;
    animation: shadow-pulse 1s infinite;
}

.widget-taber nav.tab-nav {
    padding: 0;
}

.widget-taber-content {
    box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.05);
}

/*Masonry*/
* {
    box-sizing: border-box;
}

.grid::after {
    content: '';
    display: block;
    clear: both;
}

.grid-sizer,
.grid-item {
    width: 33.333%;
}

.grid-item {
    float: left;
}

.grid-item img {
    display: block;
    max-width: 100%;
}

