    height: 1px;
    display: block;
    position: relative;
}

.line-dots::before {
    content: '';
    height: 1px;
    width: 100%;
    border-bottom: dotted 1px #222;
    position: absolute;
    top: -2px;
    left: 0;
}

.line-dots::after {
    content: '';
    height: 1px;
    width: 100%;
    border-bottom: dotted 1px #222;
    position: absolute;
    top: 2px;
    left: 0;
}

