/*Main header*/
.main-header .main-nav ul li a,
.nav-topbar li a {
    position: relative;
    padding: 15px 0;
    display: block;
}

.main-header .main-nav ul li>a::after {
    content: "";
    width: 0;
    height: 2px;
    position: absolute;
    left: 50%;
    top: -1px;
    opacity: 0.5;
    transform: translateX(-50%);
}

.main-header .main-nav ul li.mega-menu-item a::after,
.main-header .main-nav ul li ul a::after {
    content: none;
}

.main-header .main-nav ul li.current-menu-item>a::after,
.main-header .main-nav ul li:hover a::after {
    width: 50px;
    -webkit-transition: all .2s ease-out 0s;
    -moz-transition: all .2s ease-out 0s;
    -ms-transition: all .2s ease-out 0s;
    -o-transition: all .2s ease-out 0s;
    transition: all .2s ease-out 0s;
}

.main-header .main-nav ul li.menu-item-has-children>a,
.nav-topbar li.menu-item-has-children>a {
    padding-right: 15px;
}

.main-header .main-nav ul li.menu-item-has-children>a::before,
.nav-topbar li.menu-item-has-children>a::before {
    content: "\33";
    font-family: 'ElegantIcons';
    display: inline-block;
    padding-left: 5px;
    position: absolute;
    right: 0;
    font-size: 14px;
}

.nav-topbar li.menu-item-has-children>ul>li.menu-item-has-children>a::before {
    content: "\35";
    font-family: 'ElegantIcons';
    display: inline-block;
    padding-right: 8px;
    position: absolute;
    right: 0;
    font-size: 14px;
}

.nav-topbar ul.sub-menu,
.main-header .main-nav ul ul.sub-menu {
    text-align: left;
    position: absolute;
    min-width: 140px;
    background: #fff;
    left: 0;
    top: 130%;
    visibility: hidden;
    opacity: 0;
    padding: 15px 0;
    -webkit-transition: all .2s ease-out 0s;
    -moz-transition: all .2s ease-out 0s;
    -ms-transition: all .2s ease-out 0s;
    -o-transition: all .2s ease-out 0s;
    transition: all .2s ease-out 0s;
    -webkit-box-shadow: 0 4px 12px rgba(0, 0, 0, .04);
    box-shadow: 0 4px 12px rgba(0, 0, 0, .04);
    z-index: 9999;
    border: 1px solid #fafafa;
    border-radius: 3px
}

.nav-topbar>li:hover>ul.sub-menu,
.main-header ul>li:hover>ul.sub-menu {
    visibility: visible;
    opacity: 1;
    top: 100%;
}

.nav-topbar ul.sub-menu,
.main-header .main-nav ul ul.sub-menu {
    display: block;
    padding-left: 15px;
}

.nav-topbar ul.sub-menu>li>a,
.main-header .main-nav ul ul.sub-menu>li>a {
    font-weight: 500;
    line-height: 2;
    padding: 5px 0;
}

.main-header .main-nav ul ul.sub-menu>li>a:hover {
    background: none
}

.main-header .main-nav ul li h6 a {
    font-size: 1rem;
    font-weight: 600;
    line-height: 1.2;
}

/*submenu - level 3*/
.nav-topbar ul.sub-menu li {
    position: relative
}

.nav-topbar ul.sub-menu li ul.sub-menu {
    left: 100%;
    visibility: hidden;
    opacity: 0;
    top: 130%;
    margin-left: 5px;
}

.nav-topbar ul.sub-menu li:hover ul.sub-menu {
    visibility: visible;
    opacity: 1;
    top: 0;
}

