.wp-block-quote.is-style-large {
    margin: 0 0 16px;
    padding: 30px;
    background: #f4f5f9;
    border-radius: 5px;
}

.wp-block-quote.is-large p,
.wp-block-quote.is-style-large p {
    font-size: 30px;
    line-height: 1.3;
    font-style: normal;
}

.wp-block-image {
    margin-bottom: 30px;
}

.entry-main-content figure.alignleft {
    margin-right: 30px;
    margin-top: 20px;
    display: table;
}

.wp-block-image .alignleft {
    float: left;
    margin-right: 1em;
}

.wp-block-image .aligncenter>figcaption,
.wp-block-image .alignleft>figcaption,
.wp-block-image .alignright>figcaption,
.wp-block-image.is-resized>figcaption {
    display: table-caption;
    width: 100%;
    margin: 10px 0;
    caption-side: bottom
}

.wp-block-image figcaption {
    color: #6a6a6a;
    z-index: 1;
    bottom: 0;
    right: 0;
    line-height: 1.5;
    margin-bottom: 0;
    margin-top: 15px;
    text-align: center;
    font-size: 12px;
}

.tooltip-inner {
    font-size: 0.7em;
}

table {
    width: 100%;
    margin-bottom: 1.5rem;
    color: #212529;
    border-collapse: collapse;
}

table p {
    margin-bottom: 0;
}

.entry-main-content table td,
.entry-main-content table th,
.comment-content table td,
.comment-content table th {
    padding: 0.5em 1em;
    border: 1px solid #e6e6e6;
}

.entry-main-content dd,
.single-comment dd {
    margin: 0 0 1.65em;
}

.entry-main-content ul {
    list-style: disc;
}

.entry-main-content ul li,
.comment-content ul li {
    list-style: disc inside;
    line-height: 2;
}

.entry-main-content ul li.blocks-gallery-item {
    list-style: none;
}

.entry-main-content li>ul,
.entry-main-content li>ol,
.comment-content li>ul,
.comment-content li>ol {
    padding-left: 2rem;
}

.entry-main-content ol {
    padding-left: 0;
}

ol li {
    list-style: decimal inside;
    width: 100%;
    line-height: 2;
}

pre {
    border: 1px solid #eee;
    font-size: 1em;
    line-height: 1.8;
    margin: 15px 0;
    max-width: 100%;
    overflow: auto;
    padding: 1.75em;
    white-space: pre;
    white-space: pre-wrap;
    word-wrap: break-word;
    background: #fff;
    border-radius: 15px;
}

@keyframes shadow-pulse {
    0% {
        box-shadow: 0 0 0 0px rgba(88, 108, 218, 0.8);
    }

    100% {
        box-shadow: 0 0 0 5px rgba(0, 0, 0, 0);
    }
}

@keyframes shadow-pulse-big {
    0% {
        box-shadow: 0 0 0 0px rgba(88, 108, 218, 0.1);
    }

    100% {
        box-shadow: 0 0 0 20px rgba(0, 0, 0, 0);
    }
}

form.search-style-2 {
    display: flex;
    align-items: center;
}

form.search-style-2 input {
    background-color: #fff;
    border: 1px solid #ccc;
    border-right: unset;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
    color: var(--color-muted);
    min-height: 45px;
    padding: 3px 20px;
    width: 100%;
    height: 45px;
    transition: all .3s ease 0s;
    position: relative;
}

/* form.search-style-2 input:focus {
    border: 1px solid var(--color-primary);
} */

form.search-style-2 button {
    background-color: #f8f8f8;
    border: 1px solid #ccc;
    color: var(--color-primary);
    font-size: 20px;
    height: 45px;
    padding: 0 25px;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .8s;
}

form.search-style-2 button:hover {
    background-color: #f0f0f0;
}

form.search-style-2 button i {
    transform: rotateY(180deg);
    color: var(--color-muted);
    font-weight: bold;
}

form.search-style-2 button:hover i {
    color: var(--color-primary);
}

.form-search-wrapper {
    width: 100%;
}

.header-sticky .header-tools {
    display: flex;
    align-items: center;
}

.header-sticky .header-tools .header-social-network,
.header-sticky .header-tools button.search-icon,
.header-sticky .header-tools .off-canvas-toggle-cover {
    display: none !important;
}

.header-sticky.sticky-bar .header-tools button.search-icon,
.header-sticky.sticky-bar .header-tools .off-canvas-toggle-cover {
    display: block !important;
}

.header-sticky.sticky-bar .header-tools .off-canvas-toggle span,
.header-sticky.sticky-bar .header-tools .off-canvas-toggle span::before,
.header-sticky.sticky-bar .header-tools .off-canvas-toggle span::after {
    background-color: var(--color-muted)!important;
}

.header-sticky .header-tools button.search-icon i {
    font-size: 16px;
    font-weight: bold;
}

@media only screen and (max-width: 991px) {
    .header-top .header-top-right button.search-icon span {
        color: var(--color-white)!important;
    }
    .off-canvas-toggle span, .off-canvas-toggle span::before, .off-canvas-toggle span::after {
        background-color: var(--color-muted)!important;
    }
}
